"use client"
import React, { useEffect, useState } from 'react';
import { useSearchParams } from 'next/navigation';
import { wixClientServer } from '@/lib/wixClientServer';
import Link from "next/link";

const ProductList = () => {
  const searchParams = useSearchParams();
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  // Get selected category from the URL
  const categoryId = searchParams.get('cat');
  const category = searchParams.get('category'); // Adding support for both "cat" and "category" query params

  useEffect(() => {
    const fetchProducts = async () => {
      setLoading(true);
      const wixClient = await wixClientServer();

      let productQuery = wixClient.products.queryProducts();

      // Apply category filter based on the `cat` or `category` query parameter
      if (categoryId || category) {
        const selectedCategory = categoryId || category; // We use either "cat" or "category"
        productQuery = productQuery.hasSome("collectionIds", [selectedCategory]);
      }

      try {
        const res = await productQuery.find();
        setProducts(res.items);
      } catch (error) {
        console.error('Error fetching products:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, [categoryId, category]); // Re-fetch products whenever the "cat" or "category" query changes

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <div className="product_page">
        <div className="container">
          <div className="inner_">
            <ul className="product_list">
              {products.map((product, index) => (
                <li key={index}>
                  <Link href={`/product/${product.slug}`}>
                    <div className="img_wrap">
                      <img src={product.media?.items[1]?.image?.url} alt={product.name} />
                    </div>
                    <div className="name_price">
                      <label className="cat_name">{product.name}</label>
                      <label className="cat_price">${product.price?.price}</label>
                    </div>
                    <button className="add_cart">Add to Cart</button>
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductList;
